/**
 * 
 */
import {IInterfaceModel} from "./interface.model";
export enum ActionType {
    none = 0,
    list = 1,
    form = 2,
    wizard = 3,
    confirm = 4
}

export interface IConfirm {
    // confirm
    confirmTitle: string;
    // confirm
    confirmContent: string;
    // confirmok
    confirmOk?: string;
    // confirmcancel
    confirmCancel?: string;
}

export interface ISchemaForm {
    // formschema
    formSchema?: string | Array<Object>;
    // formschema
    dataSchema?: string | Object;
    // form
    title?: string;
    // 
    status?: number;
    // JSON,"/"
    path?: string;
}

export interface IWizard {
    defaultSchema?: ISchemaForm;
    // schemas?: Array<ISchemaForm>;
    actions: Array<string|IActionModel>;
}

export interface IColumn {
    content: string;
    title: string;
    name?: string;
    sort?: string;
    unit?: string;
}

export interface IClientData {
    total?: number;
    rows?: Array<any>;
}

export interface IQueryData {
    limit?: number;
    offset?: number;
    page?: number;
    where?: any;
    attributes?: any;
    order?: string;
}

export interface IList {
    columns: Array<IColumn>;
    itemToolbars?: Array<Object>;
    showPagination: boolean;
    toolbars?: Array<Object>;
    searchToolbars?: Array<Object>;
    searchActionKey?: String;
    showSearchBtn?: boolean;
    showRefreshBtn?: boolean;
    showSearchPanel?: boolean;
}

export interface IItemActionSet {
    key: string,
    condition?: string;
}


/**
 * 
 */
export interface IActionModel {
    // 
    key: string;
    // 
    icon?: string;
    // 
    title?: string;
    // 
    type: ActionType;
    // confirm 
    confirm?: IConfirm,
    // form 
    form?: ISchemaForm;
    // 
    wizard?: IWizard;
    // 
    list?: IList;
    // 
    condition?: string;
    // 
    itemActions?: Array<IItemActionSet>;
    // 
    actions?: Array<IActionModel|string>;
    // 
    interfaces?: Array<IInterfaceModel>;
    // 
    successMsg?: string;
    // 
    refreshList?: boolean ;
    // dialog
    closeDialog?: boolean;
}


